@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&family=Roboto+Mono:wght@700&display=swap');

/* 기본 스타일 */

body {
    margin: 0;

    /* ▼▼▼ 이 부분이 핵심 변경 사항입니다 ▼▼▼ */
    background-image: url('/public/background.jpg'); /* public 폴더의 이미지 사용 */
    background-size: cover; /* 이미지가 화면을 가득 채우도록 설정 */
    background-position: center; /* 이미지를 중앙에 위치 */
    background-repeat: no-repeat; /* 이미지가 반복되지 않도록 설정 */
    background-attachment: fixed; /* 스크롤해도 배경 이미지는 고정 */
    /* ▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲ */

    color: #EAEAEA;
    font-family: 'Nanum Gothic', 'Poppins', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    text-align: center;
    overflow-x: hidden;
}

