.App {
    width: 90%;

    /* ▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲▲ */
    width: 1300px;
    max-width: 1000px; /* 전체 최대 너비를 조금 늘립니다 */
    padding: 40px 20px;
}

.App-header {
    margin-bottom: 50px;
}

.App-header h1 {
    font-family: 'Roboto Mono', monospace;
    font-size: 3rem;
    font-weight: 700;
    color: #FFD700; /* Gold */
    letter-spacing: 0.2em;
    margin: 0;
}

.App-header p {
    font-size: 1.1rem;
    color: #AAA;
    margin-top: 10px;
}

/* --- Setup 화면 --- */
.setup-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    animation: fadeIn 0.8s ease-out;
}

.setup-container h2 {
    font-weight: 400;
    color: #CCC;
}

.setup-container input {
    font-family: 'Poppins', sans-serif;
    background-color: #222;
    border: 2px solid #444;
    border-radius: 8px;
    padding: 15px;
    font-size: 1.2rem;
    color: #FFF;
    width: 250px;
    text-align: center;
    transition: all 0.3s ease;
}

.setup-container input:focus {
    outline: none;
    border-color: #FFD700;
    box-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

button {
    font-family: 'Poppins', sans-serif;
    font-size: 1.2rem;
    font-weight: 600;
    padding: 15px 40px;
    border-radius: 8px;
    border: none;
    background-color: #FFD700;
    color: #121212;
    cursor: pointer;
    transition: all 0.3s ease;
    letter-spacing: 1px;
}

button:hover:not(:disabled) {
    transform: translateY(-3px);
    box-shadow: 0 5px 15px rgba(255, 215, 0, 0.3);
}

button:disabled {
    background-color: #555;
    color: #888;
    cursor: not-allowed;
}

/* --- Lottery 화면 --- */
.lottery-container {
    animation: fadeIn 0.8s ease-out;
}

.machine {
    background-color: #1a1a1a;
    padding: 40px;
    border-radius: 20px;
    border: 1px solid #333;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
}

.number-display {
    width: 250px;
    height: 250px;
    margin: 0 auto 40px;
    background: radial-gradient(circle, #2a2a2a 0%, #1a1a1a 70%);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Roboto Mono', monospace;
    font-size: 5rem;
    font-weight: 700;
    color: #FFF;
    overflow: hidden;
    border: 5px solid #333;
    box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.7);
}

.number-display span {
    display: inline-block;
}

/* 롤링 애니메이션 */
.rolling {
    animation: blurAndMove 0.08s infinite linear;
}

@keyframes blurAndMove {
    0% {
        transform: translateY(-50%);
        filter: blur(3px);
        opacity: 0.5;
    }
    100% {
        transform: translateY(50%);
        filter: blur(3px);
        opacity: 0.5;
    }
}

/* 최종 번호 애니메이션 */
.landed {
    animation: land 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) forwards;
}

@keyframes land {
    0% {
        transform: scale(1.8);
        filter: blur(2px);
        opacity: 0;
    }
    60% {
        transform: scale(0.9);
        filter: blur(0);
        opacity: 1;
    }
    100% {
        transform: scale(1);
        filter: blur(0);
        opacity: 1;
    }
}

/* 당첨 기록 */
.history {
    margin-top: 50px;

}

.history h3 {
    font-weight: 600;
    letter-spacing: 0.3em;
    color: #888;
}

.drawn-list {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    justify-content: center;
    margin-top: 20px;
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    min-height: 50px;
}

.drawn-item {
    background-color: #333;
    color: #FFD700;
    font-weight: 600;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    animation: popIn 0.5s ease;
}

/* 페이드인 애니메이션 */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* 뽑힌 번호 등장 애니메이션 */
@keyframes popIn {
    from {
        transform: scale(0.5);
        opacity: 0;
    }
    to {
        transform: scale(1);
        opacity: 1;
    }
}


/* 반응형 디자인 */
@media (max-width: 600px) {
    .App-header h1 {
        font-size: 2rem;
    }

    .number-display {
        width: 200px;
        height: 200px;
        font-size: 5rem;
    }

    .machine {
        padding: 30px;
    }
}